import React from 'react'

export default ({ data }) => {
  return (
    <section className="page-section" id="team">
      <div className="container">
        <div className="row">
          <div className="col-lg-12 text-center">
            <h2 className="section-heading text-uppercase">About Me</h2>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-5">
            <div className="team-member">
              <img className="mx-auto rounded-circle" src="https://unsplash.it/208/208/?random" alt="" />
              <h4>{data.name}</h4>
              <p className="text-muted">{data.headLine}</p>
              <ul className="list-inline social-buttons">
                {
                  data.icons && data.icons.map(({ url, icon }, index) =>
                    <li className="list-inline-item">
                      <a href={url}>
                        <img class="icons" src={icon} alt="" />
                      </a>
                    </li>
                  )
                }
              </ul>
            </div>
          </div>
          <div className="col-sm-7">
            <div className="team-member">
              <p className="text-muted">{data.text}</p>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}